/*
–î–æ–ø–æ–º—ñ–∂–Ω–∞ —Ñ—É–Ω–∫—Ü—ñ—è: –ø–µ—Ä–µ–≤—ñ—Ä—è—î, —á–∏ –µ–ª–µ–º–µ–Ω—Ç –≤–∏–¥–∏–º–∏–π —É –≤—å—é–ø–æ—Ä—Ç—ñ (–∞–Ω–∞–ª–æ–≥ onlyInViewport)
function isElementInViewport(el) {
  if (!el) return false;
  const rect = el.getBoundingClientRect();
  return (
    rect.bottom > 0 &&
    rect.top < (window.innerHeight || document.documentElement.clientHeight)
  );
}

–í–ª–∞—Å–Ω–∏–π –æ–±—Ä–æ–±–Ω–∏–∫ –∫–ª–∞–≤—ñ—à: —Ä–µ–∞–≥—É—î –¢–Ü–õ–¨–ö–ò –Ω–∞ ArrowLeft —Ç–∞ ArrowRight,
//     –ù–ï —Ä–µ–∞–≥—É—î –Ω–∞ Tab —ñ –Ω–µ –∑–∞—á—ñ–ø–∞—î —ñ–Ω—à—ñ –µ–ª–µ–º–µ–Ω—Ç–∏ –≤–≤–æ–¥—É (input, textarea, select, contenteditable)
function onDocumentKeyDown(e) {
  const active = document.activeElement;
  const activeTag =
    active && active.tagName ? active.tagName.toLowerCase() : null;
  const isEditable =
    active &&
    (active.isContentEditable ||
      activeTag === 'input' ||
      activeTag === 'textarea' ||
      activeTag === 'select');

  // –Ø–∫—â–æ —Ñ–æ–∫—É—Å —É —Ä–µ–¥–∞–≥–æ–≤–∞–Ω–æ–º—É –ø–æ–ª—ñ ‚Äî –Ω—ñ—á–æ–≥–æ –Ω–µ —Ä–æ–±–∏–º–æ
  if (isEditable) return;

  // –ü–µ—Ä–µ–∫–æ–Ω–∞—î–º–æ—Å—å, —â–æ –Ω–∞—à —Å–ª–∞–π–¥–µ—Ä –≤ –≤–∏–¥–∏–º—ñ–π —á–∞—Å—Ç–∏–Ω—ñ —Å—Ç–æ—Ä—ñ–Ω–∫–∏
  if (!isElementInViewport(document.querySelector('.hero-slider'))) return;

  if (e.key === 'ArrowLeft' || e.key === 'Left') {
    e.preventDefault(); // –∑—É–ø–∏–Ω—è—î–º–æ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—É –ø–æ–≤–µ–¥—ñ–Ω–∫—É —Å—Ç–æ—Ä—ñ–Ω–∫–∏ (–æ–ø—Ü—ñ–æ–Ω–∞–ª—å–Ω–æ)
    // –ü–µ—Ä–µ–≤—ñ—Ä—è—î–º–æ —á–∏ –º–æ–∂–Ω–∞ —Ä—É—Ö–∞—Ç–∏—Å—å –Ω–∞–∑–∞–¥
    if (
      !document
        .querySelector('.hero-button-prev')
        ?.classList.contains('swiper-button-disabled')
    ) {
      swiper.slidePrev();
      // –æ–ø—Ü—ñ–π–Ω–æ: –∑—Å—É–Ω—É—Ç–∏ —Ñ–æ–∫—É—Å –Ω–∞ prev button, —è–∫—â–æ –ø–æ—Ç—Ä—ñ–±–Ω–æ:
      // document.querySelector('.hero-button-prev')?.focus();
    }
  } else if (e.key === 'ArrowRight' || e.key === 'Right') {
    e.preventDefault();
    if (
      !document
        .querySelector('.hero-button-next')
        ?.classList.contains('swiper-button-disabled')
    ) {
      swiper.slideNext();
      // document.querySelector('.hero-button-next')?.focus();
    }
  }
}

// –ü—ñ–¥–∫–ª—é—á–∞—î–º–æ –æ–±—Ä–æ–±–Ω–∏–∫ –Ω–∞ —Ä—ñ–≤–Ω—ñ –¥–æ–∫—É–º–µ–Ω—Ç–∞ (–∑–≤–∏—á–∞–π–Ω–∞ —Ñ–∞–∑–∞)
document.addEventListener('keydown', onDocumentKeyDown);
*/

